import React from "react";
import LogoApp from "@/src/assets/images/logo2.webp";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faHome,
  faInfoCircle,
  faUser,
  faCog,
  faServer,
  faCloud,
} from "@fortawesome/free-solid-svg-icons";

interface SideBarProps {
  navItems: string[];
}

const SideBar: React.FC<SideBarProps> = ({ navItems }) => {
  const icons = [faHome, faInfoCircle, faCog, faUser, faServer, faCloud];

  return (
    <div className="flex flex-col items-center justify-between h-screen w-48 bg-primary text-sizeColor">
      <div className="flex flex-col items-center space-y-4 mt-5 flex-grow">
        <img
          className="w-16 h-16"
          src={LogoApp.src}
          alt="logo"
          onClick={() => {
            window.location.href = "/home";
          }}
        />
        {navItems.map((item, index) => (
          <a
            key={index}
            href={`#${item}`}
            className="text-center text-secondary hover:text-sizeColor font-poppins text-sm pt-2 flex items-center space-x-2"
          >
            <FontAwesomeIcon icon={icons[index]} />
            <span>{item}</span>
          </a>
        ))}
      </div>
      <p className="text-center text-secondary font-poppins text-sm mb-5">
        Created by CodeRequest
      </p>
    </div>
  );
};

export default SideBar;
